import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {Avatar, Button, Card, Col, Input, Modal, Row, Select, Tag} from "antd";
import {EditOutlined, EllipsisOutlined, SettingOutlined} from '@ant-design/icons';
import axios from 'axios';

import "./style.css"
import karaoke from "./../../karaoke.json"
import {Option} from "antd/es/mentions";

const {Meta} = Card;

const Record = (props) => (
    <tr>
        <td>{props.record.name}</td>
        <td>{props.record.position}</td>
        <td>{props.record.level}</td>
        <td>
            <Link className="btn btn-link" to={`/edit/${props.record._id}`}>Edit</Link> |
            <button className="btn btn-link"
                    onClick={() => {
                        props.deleteRecord(props.record._id);
                    }}
            >
                Delete
            </button>
        </td>

    </tr>
);

export default function Karaoke() {
    const [records, setRecords] = useState([]);
    const [users, setUsers] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [inputBgk, setInputBgk] = useState();
    let currentBgk = localStorage.getItem("bgk");

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleOk = () => {
        if(inputBgk) {
            setIsModalOpen(false);
            localStorage.setItem("bgk", inputBgk);
        }
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };
    // This method fetches the records from the database.
    useEffect(() => {
        if (!currentBgk) {
            setIsModalOpen(true);
        }
    }, []);
    useEffect(() => {
        async function getRecords() {
            const response = await fetch(`https://karaserver.onrender.com/karaoke`);
            const records = await response.json();
            setRecords(records);
        }
        async function getUsers() {
            const response = await fetch(`https://karaserver.onrender.com/users`);
            const records = await response.json();
            setUsers(records);
        }

        getRecords();
        getUsers()
        return;
    }, [records.length]);

    // This method will delete a record
    async function deleteRecord(id) {
        await fetch(`https://karaserver.onrender.com/karaoke/${id}`, {
            method: "DELETE"
        });

        const newRecords = records.filter((el) => el._id !== id);
        setRecords(newRecords);
    }

    // This method will map out the records on the table
    function recordList() {
        return records.map((record) => {
            let score = 0;
            if (record.scores && record.scores.length > 0) {
                if (currentBgk === "MC") {
                    record.scores.forEach(r => {
                        score = score + r.total;
                    })
                    score = Math.round(score / record.scores.length * 10) / 10
                } else {
                    let list = record.scores.filter(r => r.gk === currentBgk);
                    list && list.length > 0 && list.forEach(r => {
                        score = score + r.total;
                    })
                }
            }
            return (
                <Col span={8}>
                    <Link to={`/karaoke/edit/${record._id}`}>
                        <Card title={record.name} bordered={false}
                              style={{
                                  height: "210px",
                                  textAlign: "center",
                                  marginBottom: "8px",
                                  borderRadius: "8px",
                                  boxShadow: "#0066aa80 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px"
                              }}>
                            <div style={{
                                heigth: "inherit",
                                backgroundImage: 'url("data:img/png;base64,")',
                                backgroundRepeat: "no-repeat",
                                opacity: 0.1,
                                backgroundSize: "cover",
                                height: "100px",
                                width: "100px",
                                position: "absolute"
                            }}/>
                            <Tag color="geekblue" style={{marginRight: "4px"}}>{record.sbd}</Tag>

                            <span style={{fontWeight: "bold", color: "#0066aa", fontSize: '2em',}}>{score}</span>
                            <span style={{fontSize: "0.5em"}}>điểm</span>
                            <br/>
                            <strong>{record.tietmuc}</strong>
                            <br/>
                            {record.hinhthuc}
                            <br/>
                        </Card>
                    </Link>
                </Col>
            );
        });
    }

    // This following section will display the table with the records of individuals.
    return (
        <div>
            <Row>
                <h3>Danh sách thí sinh</h3>&nbsp;
                {currentBgk === "MC" && <Link to={"/karaoke/chitiet"}><Button>Chi tiết</Button></Link>}
            </Row>
            <div className="card-wrapper">
                <Modal title="Nhập tên ban giám khảo" open={isModalOpen} onOk={handleOk} closable={false}
                       maskClosable={false}
                       footer={[
                           <Button key="ok" onClick={handleOk}>
                               OK
                           </Button>]}
                >
                    {/*<Input placeholder="Ví dụ: PHT" onChange={e => {*/}
                    {/*    setInputBgk(e.target.value)*/}
                    {/*}}/>*/}
                    <Select style={{ width: "80%" }} allowClear onChange={e=>{setInputBgk(e)}}>
                        {users.map(u=>{
                            return(
                                <Option value={u.name}>{u.name}</Option>
                            )
                        })}
                    </Select>
                </Modal>
                <Row gutter={8}>
                    {recordList()}
                </Row>
            </div>
        </div>
    );
}
